.App {
  position: relative;
  display: block;
  margin: 0 auto;
  padding: 20px;
  max-width: 1020px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #F5F6FA;
  color: #192A56; }
  .App > div canvas {
    max-width: calc(100vw - 20px);
    max-height: 100vh; }

.App-header {
  height: 35px; }

.App-logo {
  height: 35px; }

.App-nav {
  position: relative;
  float: right;
  line-height: 35px;
  font-size: 17.5px;
  z-index: 1; }
  .App-nav a {
    position: relative;
    z-index: 3;
    text-decoration: none;
    color: #192A56;
    margin-right: 10px; }
    .App-nav a:last-of-type {
      margin-right: 0; }
    .App-nav a::before {
      content: "";
