      -o-transition: 0.2s;
      transition: 0.2s;
      z-index: -1; }
    .App-nav a.active::before, .App-nav a:hover::before {
      opacity: 1; }
    .App-nav a.active::before, .App-nav a.active:hover::before {
      background-color: #E1B12C; }
    .App-nav a:hover::before {
      background-color: #DCDDE1; }

.App-footer a {
