@import url("https://fonts.googleapis.com/css?family=Roboto:300,400");
html, body {
  margin: 0;
  padding: 0;
  background: #F5F6FA;
  color: #192A56;
  font-family: 'Roboto light', sans-serif;
  font-size: 17.5px; }

.big {
  font-size: 25px; }

.clearFix {
  overflow: hidden;
  height: 1%; }

.centerText {
  text-align: center; }

h1.big {
  font-weight: 300; }

.changingList {
  display: inline-block;
  position: relative;
  padding: 0;
  margin: 0;
  top: 3px;
  height: 18px;
  overflow: hidden; }
  .changingList span {
    display: block;
    position: relative;
    opacity: 1;
    line-height: 18px;
    -webkit-animation: changeList 4s infinite alternate;
            animation: changeList 4s infinite alternate; }

@-webkit-keyframes changeList {
  0% {
    top: 0; }
  20% {
    top: 0; }
  40% {
    top: -18px; }
  60% {
    top: -18px; }
  80% {
    top: -36px; }
  100% {
    top: -36px; } }

@keyframes changeList {
  0% {
    top: 0; }
  20% {
    top: 0; }
  40% {
    top: -18px; }
  60% {
    top: -18px; }
  80% {
    top: -36px; }
  100% {
    top: -36px; } }



